import { Layout, Menu } from 'antd';
import React from 'react';
import { Link } from 'react-router-dom';

const { Header } = Layout;

const menus = [

  { key: ' ', label: ' ' },
  { key: ' ', label: <Link to='/suggest'> </Link> },
  { key: ' ', label: ' ' },
  { key: '', label: '' },
  { key: ' ', label: ' ' }
];

const NavBar = () => {
  return (
    <Layout>
      <Header
        style={{
          display: 'flex',
          justifyContent: 'center',
        }}
      >
        <div />
        <Menu
          theme="dark"
          mode="horizontal"
          items={menus}
          style={{
            flex: 1,
            minWidth: 0,
            justifyContent: 'center',
          }}
        />
      </Header>
    </Layout>
  )
}

export default NavBar