import React from 'react';
import { Card, Col, Row } from 'antd';
import { Link } from 'react-router-dom';
import { america, france, greece, japan1, jeju, singapol, switzerland, venezia, mongol, england, india, german } from '../Ad_Image'

const imgs = [
  { src: america, alt: '', title: ' ', descriptions: '   ' },
  { src: france, alt: '', title: ' ', descriptions: '   ' },
  { src: greece, alt: '', title: ' ', descriptions: '  ' },
  { src: japan1, alt: '', title: ' ', descriptions: '   ' },
  { src: jeju, alt: '', title: ' ', descriptions: '   ' },
  { src: singapol, alt: '', title: ' ', descriptions: '    ' },
  { src: switzerland, alt: '', title: ' ', descriptions: '  , ' },
  { src: venezia, alt: '', title: ' ', descriptions: '    ' },
  { src: mongol, alt: '', title: ' ', description: ',    ' },
  { src: england, alt: '', title: ' ', description: '   ' },
  { src: india, alt: '', title: ' ', description: '     ' },
  { src: german, alt: '', title: ' ', description: '   ' },
];

const { Meta } = Card;

const Adver = () => (
  <Row gutter={[16, 16]}>
    {imgs.map((img, index) =>
      <Col key={index} lg={4} md={6} sm={12} xs={24}>
        <Link>
          <Card
            hoverable
            style={{
              width: '120px',
            }}
            cover={<img alt={`${img.alt}`} src={img.src} style={{ height: '100px' }} />}
          >
            <Meta title={img.alt}/>
          </Card>
        </Link>
      </Col>
    )}
  </Row>
);
export default Adver;